export default{
	data(){
		return{
			windowHeight: '',
			id:'',
			coupon:[],
			del_id:[],
		}
	},
	onLoad() {
		var that = this;
		that.windowHeight = this.statusBarHeight + 44;
		uni.getStorage({
			key: 'userinfo',
			success: function(res) {
				that.id = res.data.id;
				that.getinfo();
			}
		})
	},
	methods:{
		getinfo:function(){
			var that=this;
			uni.request({
				url: this.serveipd + "/api/merchant/money/groupTickets",
				method: 'GET',
				data: {
					merchantId:that.id,
				},
				success: res => {
					that.coupon = res.data.data.datas;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		checkboxChange: function (e) {
			var items = this.coupon,
				values = e.detail.value;
				console.log(items);
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if(values.includes(item.name)){
					this.$set(item,'checked',true)
				}else{
					this.$set(item,'checked',false)
				}
			}
		},
		fastdel:function(){
			console.log(this.coupon);
			var that=this;
			var items = this.coupon;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				this.$set(item,'checked',true);
			}
			
		},
		del:function(){
			var that=this;
			that.del_id=[];
			var items = this.coupon;
			console.log(items);
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if(item.checked == true){
					that.del_id=that.del_id.concat(item.id);
				}
			}
			if(that.del_id.length==0){
				uni.showToast({
					title: '请选择要删除的记录',
					icon:'none',
					mask: false,
					duration: 1500
				});
			}else{
				uni.request({
					url: this.serveipd+"/api/merchant/money/groupTicket/delete",
					method: 'GET',
					header: {
						'content-type': 'application/x-www-form-urlencoded',
					},
					data: {
						arrs:that.del_id.toString(),
					},
					success: res => {
						uni.showToast({
							title: res.data.msg,
							icon:'none',
							mask: false,
							duration: 1500
						});
						that.getinfo();
					},
					fail: () => {},
					complete: () => {}
				});
			}

		},
		back: function() {
			uni.navigateBack({
				delta: 1
			});
		},
		preserve:function(){
			uni.navigateBack({
				delta: 1
			});
		}
	}
}
